.navbar {
    width: 100%;
    height: 90px;
    background-color: rgb(0, 0, 0);
    display: flex;
    justify-content: flex-end;
    align-items: center;
    border-radius: 5px;
    box-shadow: 1px 2px 10px 5px #34333f;

  }
  
  .links {
    margin-right: 50px;
    display: flex;
    align-items: center;
  }
  
  .links a {
    text-decoration: none;
    color: rgb(252, 251, 251);
    margin-left: 20px;
    font-size: 30px;
  }